import React from "react";
import { useState } from "react";
import {
  Grid,
  TextField,
  TextareaAutosize,
  Button,
  Typography,
  IconButton,
} from "@mui/material";
import Validator from "../../validators/validator";
import WeightedAverageService from "../../services/averages/weightedAverageService";
import { OverlayTrigger, Popover } from "react-bootstrap";
import InfoIcon from "@mui/icons-material/Info";

export interface Numbers {
  allNumbers: string;
  allWages: string;
  numbersWagesShow: string;
}

export interface NewNumbers {
  number: string;
  wage: string;
}

export interface Response {
  solution: string;
  hint: string;
}

export interface NumberError {
  numberError: string;
  wageError: string;
}

const WeightedAverage = () => {
  const [numbers, setNumbers] = useState<Numbers>({
    allNumbers: "",
    allWages: "",
    numbersWagesShow: "",
  });
  const [newNumbers, setNewNumbers] = useState<NewNumbers>({
    number: "",
    wage: "",
  });
  const [res, setRes] = useState<Response>({
    solution: "",
    hint: "",
  });

  const [errors, setErrors] = useState<NumberError>({
    numberError: "",
    wageError: "",
  });

  const [hint, setHint] = useState<boolean>(false);

  const showOrHideHint = () => {
    setHint(!hint);
  };

  const [pop, setPop] = useState<boolean>(false);

  const setPopover = (val: boolean) => {
    setPop(val);
  };

  const addNumbers = () => {
    let newErrorState = { ...errors };
    newErrorState.numberError = "";
    newErrorState.wageError = "";
    let valid = true;
    if (!Validator.checkIfIsNotEmpty(newNumbers.number)) {
      newErrorState.numberError = Validator.notNumbersMessage;
      valid = false;
    } else if (!Validator.checkIfIsValidNumber(newNumbers.number)) {
      newErrorState.numberError = Validator.notNumberMessage;
      valid = false;
    }
    if (!Validator.checkIfIsNotEmpty(newNumbers.wage)) {
      newErrorState.wageError = Validator.notNumbersMessage;
      valid = false;
    } else if (!Validator.checkIfIsValidNumber(newNumbers.wage)) {
      newErrorState.wageError = Validator.notNumberMessage;
      valid = false;
    }
    setErrors(newErrorState);
    if (valid === true) {
      let tempNumber = numbers.allNumbers;
      let tempWage = numbers.allWages;
      let tempString = numbers.numbersWagesShow;
      if (tempNumber.length > 0) {
        tempNumber = tempNumber + "," + newNumbers.number;
        tempWage = tempWage + "," + newNumbers.wage;
        tempString =
          tempString + "," + newNumbers.number + "(" + newNumbers.wage + ")";
        setNumbers({
          ...numbers,
          allNumbers: tempNumber,
          allWages: tempWage,
          numbersWagesShow: tempString,
        });
      } else {
        setNumbers({
          ...numbers,
          allNumbers: newNumbers.number,
          allWages: newNumbers.wage,
          numbersWagesShow: newNumbers.number + "(" + newNumbers.wage + ")",
        });
      }
      setNewNumbers({ ...newNumbers, number: "", wage: "" });
    }
  };

  const findWeightedAverage = () => {
    if (numbers.allNumbers && numbers.allNumbers.length > 0) {
      WeightedAverageService.findWeightedAverage(numbers)
        .then((response) => {
          setRes(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    } else {
      setErrors({
        ...errors,
        numberError: "Dodaj liczby najpierw",
        wageError: "",
      });
    }
  };

  const clearList = () => {
    setNumbers({
      ...numbers,
      allNumbers: "",
      allWages: "",
      numbersWagesShow: "",
    });
  };

  const popover = (
    <Grid marginLeft={2}>
      {pop && (
        <Popover
          id="popover-basic"
          onClick={() => {
            setPopover(false);
          }}
          onMouseLeave={() => {
            setPopover(false);
          }}
        >
          <Grid marginTop={-1}>
            <Popover.Body>W nawiasie wyświetlana jest waga liczby</Popover.Body>
          </Grid>
        </Popover>
      )}
    </Grid>
  );

  return (
    <Grid
      container
      justifyContent="center"
      display="flex"
      marginTop={2}
      marginBottom={50}
    >
      <Grid item width={1000}>
        <Grid
          item
          justifyContent="center"
          alignItems="center"
          paddingBottom={2}
          marginTop={0}
          paddingTop={0}
        >
          <Typography fontSize={40} color="#3596FC" textAlign="center">
            Średnia ważona
          </Typography>
        </Grid>
        <Grid item width={1000} display="flex">
          <Grid item width={790} display="flex">
            <Grid item width={370}>
              <TextField
                fullWidth
                placeholder="Wpisz liczbę..."
                value={newNumbers.number ? newNumbers.number : ""}
                onChange={(event) =>
                  setNewNumbers({
                    ...newNumbers,
                    number: event.target.value,
                  })
                }
                helperText={errors.numberError ? errors.numberError : ""}
              ></TextField>
            </Grid>
            <Grid item width={370} marginLeft={5}>
              <TextField
                fullWidth
                placeholder="Wpisz wagę liczby..."
                value={newNumbers.wage ? newNumbers.wage : ""}
                onChange={(event) =>
                  setNewNumbers({
                    ...newNumbers,
                    wage: event.target.value,
                  })
                }
                helperText={errors.wageError ? errors.wageError : ""}
              ></TextField>
            </Grid>
          </Grid>
          <Grid item width={150} marginLeft={2} marginTop={1}>
            <Button
              style={{ background: "#33BCF2" }}
              fullWidth
              variant="contained"
              onClick={() => {
                addNumbers();
              }}
            >
              Dodaj liczbę
            </Button>
          </Grid>
          <Grid display="flex">
            <Typography fontSize={36}>&nbsp;</Typography>
          </Grid>
        </Grid>
        <Grid item marginTop={2}>
          <Grid item display="flex">
            <Grid item>
              <Typography fontSize={16} color="#2C74B3" textAlign="left">
                Twoje liczby
              </Typography>
            </Grid>
            <Grid item marginLeft={1} maxHeight={20}>
              <OverlayTrigger
                trigger="click"
                rootClose
                placement="right"
                overlay={popover}
              >
                <IconButton
                  style={{ height: "100%" }}
                  onClick={() => {
                    setPopover(true);
                  }}
                >
                  <InfoIcon />
                </IconButton>
              </OverlayTrigger>
            </Grid>
          </Grid>
          <Grid item display="flex">
            <Grid item>
              <TextareaAutosize
                value={numbers.numbersWagesShow ? numbers.numbersWagesShow : ""}
                style={{ width: 780, resize: "vertical" }}
              />
            </Grid>
            <Grid item width={150} marginLeft={2}>
              <Button
                fullWidth
                style={{ background: "#FF876C" }}
                variant="contained"
                onClick={() => {
                  clearList();
                }}
              >
                Wyczyść
              </Button>
            </Grid>
          </Grid>
          <Grid item marginTop={2}>
            <Button
              fullWidth
              variant="contained"
              style={{ background: "#4683E6" }}
              onClick={() => {
                findWeightedAverage();
              }}
            >
              Oblicz
            </Button>
          </Grid>
        </Grid>
      </Grid>
      <Grid item marginTop={3}>
        <Grid item>
          <Typography fontSize={16} color="#2C74B3" textAlign="left">
            Wynik
          </Typography>
        </Grid>
        <TextareaAutosize
          value={res.solution ? res.solution : ""}
          style={{ width: 1000, resize: "vertical" }}
        />
      </Grid>
      <Grid item marginTop={3} width={1000}>
        <Button
          style={{
            backgroundColor: "#21b6ae",
          }}
          fullWidth
          variant="contained"
          onClick={() => {
            showOrHideHint();
          }}
        >
          Pokaż wskazówkę
        </Button>
      </Grid>
      <Grid item marginTop={2}>
        {hint && (
          <Grid item>
            <Grid item>
              <Typography fontSize={16} color="#2C74B3" textAlign="left">
                Wskazówka
              </Typography>
            </Grid>
            <TextareaAutosize
              value={res.hint ? res.hint : ""}
              style={{ width: 1000, resize: "vertical" }}
            />
          </Grid>
        )}
      </Grid>
    </Grid>
  );
};

export default WeightedAverage;
